import opg from 'ts/opg.ts';
import Panel from 'ts/ui/Panel.ts';
import Table from 'ts/ui/Table.ts';
import {Combo} from 'ts/ui/Combo';
import {Languages} from 'ts/util/Languages';
import PopUp from "ts/ui/Popup";
import {url} from "ts/util/utils";
import {api} from "ts/util/api";
import {request, string, dateTime, is, url, convert, format, array} from  'ts/util/utils';


opg.api({
    'getdigitalpresslistbyuser!post': 'Operation/Getdigitalpresslistbyuser',
    'adddigitalpress!post':'Operation/Adddigitalpress',
    'upload!UPLOAD': 'upload/files'
});

const lpg = Languages.package;

let clng='en';

if(localStorage.getItem("lngPkg")!=null|| localStorage.getItem("lngPkg")!=undefined)
{
    const currentlng=localStorage.getItem("lngPkg");
    clng=currentlng.replace(/["""]/g,"");
}


$("#btnReset").click(function() {
    location.reload(true);
});

$("#btnSaveMain").click(function() {
    let param=$("#tbMain").fieldsToJson();
    let pressfile="";

    for(var i=0;i<param.icon.length;i++) {
        if(param.icon[i]!="") {
            pressfile+=param.icon[i]+"|";
        }
    }
    param.pressfile=pressfile;
    opg.api.adddigitalpress(param, function (data) {
        location.reload(true);
    });
});




